import {
  ClassAttributes,
  DetailedHTMLProps,
  FC,
  InputHTMLAttributes,
  useContext,
  useCallback,
  useMemo,
} from 'react';
import classNames from 'classnames';

// Styles
import './Input.scss';

// Types
import { TestMetadata } from 'types';

// Analytics
import {
  AnalyticsContext,
  handleAnalyticsEvent,
  TAnalyticsEventData,
} from '@dtci/dash/analytics';

export interface InputProps
  extends DetailedHTMLProps<
    InputHTMLAttributes<HTMLInputElement>,
    HTMLInputElement
  > {
  /** value of the input field */
  value?: number | string;
  /** Boolean to determine if field is required */
  required?: boolean;
  /** If label is not visible, use aria-label for screen readers */
  labelText?: string;
  /** ref form inner input **/
  inputRef?: ClassAttributes<HTMLInputElement>['ref'];
  /** Width of the Input */
  width?: 'small' | 'medium' | 'large' | 'auto';
  // For use with nested components, ie. inputProps
  'data-testid'?: string;
  testMetadata?: TestMetadata;
  /** Error State */
  isError?: boolean;
  /** Disabled State */
  isDisabled?: boolean;
  /** Border Flush */
  flush?: 'left' | 'right' | 'flush';
  /** Indicate if input should have a border */
  bordered?: boolean;
  /** Additional event information to pass to analytics tracking */
  analyticsEventData?: TAnalyticsEventData;
}
/** Input components are useful for editing text in forms */
const Input: FC<InputProps> = ({
  className,
  labelText,
  inputRef,
  type = 'text',
  required,
  width = 'small',
  testMetadata,
  isError = false,
  flush,
  bordered = true,
  isDisabled,
  analyticsEventData,
  ...otherProps
}) => {
  const inputClassNames = useMemo(
    () =>
      classNames(
        'sb-input',
        {
          'sb-input_error': isError,
          'sb-input__is-required': required,
          'sb-input__is-disabled': isDisabled,
          'sb-input__size-small': width === 'small',
          'sb-input__size-medium': width === 'medium',
          'sb-input__size-large': width === 'large',
          'sb-input__size-auto': width === 'auto',
          'sb-input__flush-right': flush === 'right',
          'sb-input__flush': flush === 'flush',
          'sb-input__flush-left': flush === 'left',
          'sb-input__borderless': !bordered,
        },
        className,
      ),
    [className, required, width, isError, flush, bordered, isDisabled],
  );

  const analyticsContext = useContext(AnalyticsContext);
  const { onFocus, onBlur, onKeyDown } = otherProps;

  const handleOnFocus = useCallback(
    (e: React.FocusEvent<HTMLInputElement>): void => {
      onFocus?.(e);
      handleAnalyticsEvent({
        analyticsContext,
        analyticsEventData,
        componentType: 'Input',
        actionName: 'focus',
        label:
          otherProps?.['aria-label'] ||
          otherProps?.name ||
          labelText ||
          className ||
          testMetadata?.['data-testid'] ||
          '',
        value: [{ text: e.target.value }],
      });
    },
    [
      analyticsContext,
      analyticsEventData,
      className,
      labelText,
      onFocus,
      otherProps,
      testMetadata,
    ],
  );

  const handleOnBlur = useCallback(
    (e: React.FocusEvent<HTMLInputElement>): void => {
      onBlur?.(e);
      handleAnalyticsEvent({
        analyticsContext,
        analyticsEventData,
        componentType: 'Input',
        actionName: 'blur',
        label:
          otherProps?.['aria-label'] ||
          otherProps?.name ||
          labelText ||
          className ||
          testMetadata?.['data-testid'] ||
          '',
        value: [{ text: e.target.value }],
      });
    },
    [
      analyticsContext,
      analyticsEventData,
      className,
      labelText,
      onBlur,
      otherProps,
      testMetadata,
    ],
  );

  const handleOnKeyDown = useCallback(
    (e: React.KeyboardEvent<HTMLInputElement>): void => {
      onKeyDown?.(e);

      if (e.key !== 'Enter') return;

      handleAnalyticsEvent({
        analyticsContext,
        analyticsEventData,
        componentType: 'Input',
        actionName: 'enter',
        label:
          otherProps?.['aria-label'] ||
          otherProps?.name ||
          labelText ||
          className ||
          testMetadata?.['data-testid'] ||
          '',
        value: [{ text: e.currentTarget.value }],
      });
    },
    [
      analyticsContext,
      analyticsEventData,
      className,
      labelText,
      otherProps,
      testMetadata,
      onKeyDown,
    ],
  );

  return (
    <input
      {...otherProps}
      onFocus={handleOnFocus}
      onBlur={handleOnBlur}
      onKeyDown={handleOnKeyDown}
      className={inputClassNames}
      required={required}
      aria-required={required}
      aria-label={labelText}
      ref={inputRef}
      type={type}
      {...testMetadata}
    />
  );
};

export default Input;
