import { ComponentProps, FC } from 'react';
import InputMask from 'react-input-mask';

// Components
import Input, { InputProps } from '../Input';

// Styles
import './MaskedInput.scss';

// Types
import { TestMetadata } from '../types';

// NOTE: react-input-mask does not type the children props correctly,
// determining the type from the docs
// https://www.npmjs.com/package/react-input-mask#children--function
type InputMaskChildrenProps = Omit<
  ComponentProps<'input'>,
  | 'onChange'
  | 'onPaste'
  | 'onMouseDown'
  | 'onFocus'
  | 'onBlur'
  | 'value'
  | 'disabled'
  | 'readOnly'
  // Including 'width` which has a different type from native and SB Input
  // for convenience
  | 'width'
>;

export interface MaskedInputProps {
  /* Mask to apply to the input */
  mask: string;
  /* Filler character in the value */
  maskCharacter?: string | null;
  /* Input props */
  inputProps?: InputProps;
  /* Show mask without user interaction */
  alwaysShowMask?: boolean;
  /* Test Data */
  testMetadata?: TestMetadata;
}

const MaskedInput: FC<MaskedInputProps> = ({
  mask,
  maskCharacter,
  alwaysShowMask,
  testMetadata,
  inputProps = {},
}) => {
  const {
    value,
    onChange,
    onBlur,
    readOnly,
    disabled,
    inputRef,
    ...otherInputProps
  } = inputProps;
  return (
    <InputMask
      mask={mask}
      maskChar={maskCharacter}
      value={value}
      onChange={onChange}
      onBlur={onBlur}
      alwaysShowMask={alwaysShowMask}
      readOnly={readOnly}
      disabled={disabled}
    >
      {(maskProps: InputMaskChildrenProps) => (
        <Input
          {...otherInputProps}
          {...maskProps}
          {...testMetadata}
          disabled={disabled}
          readOnly={readOnly}
          inputRef={inputRef}
        />
      )}
    </InputMask>
  );
};

export default MaskedInput;
